      h1 Gestin comunitaria en el marco de la agroecologa

    .row.align-items-center.justify-content-center.mb-5(data-aos="fade-left")
      p.mb-3 La gestin comunitaria hace referencia a la participacin de la poblacin de forma individual o grupal, mediante estrategias y operaciones enfocadas en solucionar problemticas o alcanzar un objetivo. Esta permite que los procesos de participacin ciudadana sean estructurados en torno a las necesidades y demandas de la comunidad, a la par, permiten que se consoliden las interrelaciones humanas y la transformacin de la comunidad.
      p.mb-5 Por su parte, segn la Organizacin de las Naciones Unidas para la Alimentacin y la Agricultura - FAO, menciona que: 

      .cajon.color-secundario.p-4.fnd-1 
