          .col-md-9.mb-md-0
            p La agroecologa desde la dcada de 1920 fue definida por cientficos e investigadores para aludir a la aplicacin de principios ecolgicos en la agricultura. Sin embargo, segn la FAO, no fue sino hasta la dcada de los aos ochenta, que la agroecologa fue tomada como una disciplina dentro de la ecologa, la agronoma y la etnobotnica. La agroecologa constituye un espectro de diferentes corrientes tericas, de ah que, la FAO ha constituido una base de datos, definiciones para lo cual ha reunido varias, de diversas fuentes realizadas por el gobierno, cientficos, acadmicos, ciudadanos, etc.

        

    .row.justify-content-center.d-flex.align-items-center.mb-5
      .col-lg-8
        p.mb-3 Como disciplina, la Agroecologa analiza las interacciones entre los componentes del sistema productivo en las sociedades modernas, relacionando la ciencia con el saber tradicional para la generacin de alimentos de forma sostenible en combinacin con los elementos biofsicos y socioeconmicos, que se basan en los pilares y dimensiones del desarrollo sostenible, las cuales son la econmica, la social y la ambiental. 
        p.mb-3 Adems, como lo sustenta Nova (2022), la Agroecologa constituye un paradigma a la agricultura, una opcin viable frente las prcticas convencionales, que normalmente emplean tecnologas apoyadas y direccionadas en la aplicacin de qumicos, prcticas de monocultivos, y en general un uso irracional y contaminacin del suelo, agua y de los recursos naturales. Segn la FAO, existen 10 elementos de la agroecologa que se presentan a continuacin: 

