    p.mb-5 Estos elementos orientan el proceso de transformacin hacia sistemas agrcolas y alimentarios sostenibles, mediante la explicacin de las caractersticas, prcticas, entorno favorable e innovaciones de los sistemas agroecolgicos, sin desconocer los aspectos contextuales. Los cuales estn interrelacionados y son interdependientes, cada uno de estos aspectos se presenta continuacin: 
    
