            h5.mt-5 Diversidad
            p.mb-3 La diversificacin es fundamental en las transiciones agroecolgicas para garantizar la seguridad alimentaria y la nutricin y, al mismo tiempo, conservar, proteger y mejorar los recursos naturales. La diversificacin agroecolgica refuerza la resiliencia ecolgica y socioeconmica mediante la creacin de nuevas oportunidades de mercado.
            p.mb-3 Por ejemplo, la diversidad de cultivos y animales reduce el riesgo de fracaso ante el cambio climtico.
        
