              .col-md-6.mb-4.mb-md-0.d-flex.align-items-start
                p <b>Sinergias </b> <br> <br> Crear sinergias potencia las principales funciones de los sistemas alimentarios, lo que favorece la produccin y mltiples servicios ecosistmicos. Mediante la optimizacin de las sinergias biolgicas, las prcticas agroecolgicas potencian las funciones ecolgicas, lo que da lugar a un aumento de la eficiencia en el uso de los recursos y de la resiliencia.


              .col-md-6.d-flex.align-items-start
                figure
