          .col-md-6.mb-4.py-3
            .row.justify-content-center
              .col-md-6.mb-4.mb-md-0.d-flex.align-items-start
                p <b>Eficiencia</b> <br> <br>Las prcticas agroecolgicas innovadoras producen ms utilizando menos recursos externos. La mayor eficiencia en el uso de los recursos es una propiedad emergente de los sistemas agroecolgicos que planifican y gestionan detenidamente la diversidad con miras a crear sinergias entre diferentes componentes del sistema.


