            h5.mt-5 Reciclaje
            p.mb-3 Reciclar ms significa una produccin agrcola con menos costos econmicos y ambientales. El desperdicio es un concepto humano: <b>en los ecosistemas naturales no existe. </b>

            p.mb-3 Al imitar los ecosistemas naturales, las prcticas agroecolgicas favorecen los <b>procesos biolgicos</b> que impulsan el reciclaje de los nutrientes, la biomasa y el agua de los sistemas de produccin, con lo que se aumenta la eficiencia en el uso de los recursos y se reduce al mnimo el desperdicio y la contaminacin.

