            h5.mt-5 Resiliencia
            p.mb-3 Mejorar la resiliencia de las personas, las comunidades y los ecosistemas es fundamental para lograr sistemas alimentarios y agrcolas sostenibles. Los sistemas agroecolgicos diversificados son ms resilientes, esto es, porque tienen una mayor capacidad para recuperarse de las perturbaciones, en particular de fenmenos meteorolgicos extremos como sequa, inundaciones o huracanes, y para resistir el ataque de plagas y enfermedades.

