            h5.mt-5 Valores humanos y sociales

            p.mb-3 Proteger y mejorar los medios de vida, la equidad y el bienestar social es fundamental para lograr sistemas alimentarios y agrcolas sostenibles. 
            p.mb-3 La agroecologa hace especial hincapi en valores como la dignidad, la equidad, la inclusin y la justicia, que contribuyen a la mejora de los medios de vida.
            p.mb-3 Los enfoques agroecolgicos dotan a las personas y comunidades de los medios para superar la pobreza, el hambre y la malnutricin, al tiempo que promueven los Derechos Humanos.

          .col-md-7
