            h5.mt-5 Cultura y tradiciones alimentarias

            p.mb-3 Mediante el apoyo a unas dietas saludables, diversificadas y culturalmente apropiadas, la agroecologa contribuye a la seguridad alimentaria y la nutricin, al tiempo que mantiene la salud de los ecosistemas. 
            p.mb-3 La agricultura y la alimentacin son componentes esenciales del patrimonio de la humanidad, la cultura y las tradiciones alimentarias cumplen un papel social fundamental a la hora de moldear el comportamiento humano. 
            p.mb-3 Los sistemas alimentarios actuales han creado una desconexin entre los hbitos alimentarios y la cultura, lo que ha contribuido a una situacin en la que coexisten el hambre y la obesidad, en un mundo que produce alimentos suficientes para alimentar a toda su poblacin.

