            p.mb-3 Para lograr una alimentacin y una agricultura sostenibles es necesario adoptar mecanismos de gobernanza responsables y eficaces a diferentes escalas, de la local a la nacional y la mundial. 
            p.mb-3 La agroecologa requiere una gobernanza responsable y eficaz para respaldar la transicin a sistemas alimentarios y agrcolas sostenibles. 
            p.mb-3 Se necesitan mecanismos de gobernanza transparentes, inclusivos y basados en la rendicin de cuentas para crear un entorno favorable que ayude a los productores a transformar sus sistemas siguiendo conceptos y prcticas agroecolgicos.

