    p.mb-5 La agroecologa toma en consideracin la gestin comunitaria y, por ende, la normativa vara segn la aplicacin socioeconmica de las sociedades modernas. Para Martnez (2004), la estrategia y finalidad de la agroecologa es crear alternativas de desarrollo social, econmico y ambiental racional, incorporando prcticas y tcnicas de produccin agrcola tradicional, pero de forma sustentable, generando el menor impacto negativo a los recursos naturales, es decir, garantizando y conservando al medio ambiente. Mediante una estrategia agroecolgica enfocada a niveles de aplicacin o dimensiones (social, econmico y ambiental), donde la normativa puede variar segn los principios comunitarios y organizativos de cada regin. 


