              p.mb-0 As mismo, el MADR en la Resolucin 0187 de 2006 establece que la norma ecolgica regula la produccin primaria, el procesamiento y la comercializacin de productos alimenticios, regido por estndares de la agricultura orgnica, para la comercializacin en territorio nacional. 

