        .tarjeta.color-acento-botones.fnd-5.p-4(titulo="Reuniones con la <br>comunidad")
          .row
            .col-xl-6.align-self-center
              p Se trata de reunir a la gente de la comunidad para que comparta informacin acerca de un problema y que esta se centre en objetivos especficos. Las reuniones pueden convocar a toda la comunidad o a representaciones de grupos involucrados. Los participantes estarn de acuerdo en compartir sus ideas sobre problemas comunes y contribuir con posibles soluciones. 
