          .row
            .col-xl-6.align-self-center
              p El propsito es observar sistemticamente objetos, personas, eventos y relaciones. Se utiliza durante todo el proceso de evaluacin del proyecto. Es un medio para recoger informacin sobre cmo interactan las personas y cmo se desenvuelven en sus actividades cotidianas. Se puede realizar de manera individual o con la comunidad.
                
            .col-xl-6.align-self-center
              figure
